.footer {
  background-color: var(--light-color);
  padding-top: 85px;
  padding-bottom: 80px;

  &__logo {
    margin-bottom: 15px;
  }

  &__content {
    display: flex;
    margin-bottom: 70px;
  }

  &__info {
    width: 50%;
  }

  &__descr {
    margin-bottom: 40px;
    max-width: 495px;
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    letter-spacing: 0.016em;
    color: var(--grey-color);
  }

  &__social {
    display: flex;
    align-items: center;
  }


  &__nav {
    display: flex;
    column-gap: 200px;
    justify-content: space-around;

    &-block {
      display: block;
    }

    &-block-title {
      margin: 0;
      margin-bottom: 14px;
      font-weight: 700;
      font-size: 24px;
      line-height: 150%;
      letter-spacing: 0.016em;
      color: var(--dark-color);
    }
  }

  &__copy {
    text-align: center;
    font-weight: 400;
    font-size: 20px;
    line-height: 36px;
    color: var(--grey-color);
  }
}

.social__item:not(:last-child) {
  margin-right: 24px;
}

.nav__list {
}

.footer__nav-link {
  font-weight: 400;
  font-size: 20px;
  line-height: 50px;
  color: var(--grey-color);
  transition: fill 0.2s ease-in;

  &:hover {
    color: var(--orange-color);
  }
}

.social__link svg {
  fill: #5C5C5C;
  transition: fill 0.2s ease-in;

  &:hover {
    fill: var(--green-color);
  }
}
