.features {
  margin-top: 100px;
  margin-bottom: 100px;

  &__heading {
    margin-bottom: 90px;
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
    text-align: center;
  }

  &__descr {
    margin: 0 auto;
    max-width: 660px;
    text-align: center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 20px;
    row-gap: 20px;
  }

  &__item {
    padding: 80px 55px;
    border: 1px solid #F5EEE7;
    border-radius: 10px;
    transition: all 0.5s ease-in;

    &:hover {
      border: none;
      background: linear-gradient(156.79deg, rgba(255, 248, 241, 0.45) 0.44%, #FFF8F2 61.76%, #E0FBE5 134.86%);
      box-shadow: 25px 25px 100px rgba(166, 161, 157, 0.2);
    }

    &-img {
      width: 84px;
      height: 77px;
      margin: 0 auto;
      margin-bottom: 24px;
      justify-content: center;
      align-items: center;

      img {
        border-radius: 12px;
      }
    }

    &-title {
      margin: 0 auto;
      margin-bottom: 24px;
      font-weight: 700;
      font-size: 24px;
      line-height: 150%;
      letter-spacing: 0.016em;
      text-align: center;
      color: var(--dark-color);
    }

    &-descr {
      margin: 0 auto;
      font-size: 18px;
      line-height: 150%;
      text-align: center;
      letter-spacing: 0.016em;
      color: var(--grey-color);
    }
  }

}

