// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Gilroy", sans-serif;
  --content-width: 1330px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --orange-color: #F05830;
  --green-color: #236E57;
  --dark-color: #0D0D0D;
  --grey-color: #807D7D;
  --light-color: #ffffff;
  --bg-color: #FFF8F1;
}
