.download {
  position: relative;
  padding: 80px 0 220px 0;

  &__image {
    position: absolute;
    right: 0;

    img {
      width: 690px;
    }
  }

  &__content {
    max-width: 590px;
    margin: 100px auto 0 0;
  }

  &__heading {
    margin-bottom: 60px;
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
  }

  &__descr {
    margin: 0;
    font-size: 18px;
    max-width: 500px;
  }
}
