.hero {
  position: relative;
  padding-top: 205px;
  padding-bottom: 205px;

  &__content {
    max-width: 600px;
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 56px;
    line-height: 130%;
    letter-spacing: 0.016em;
  }

  &__descr {
    margin: 0;
    margin-bottom: 70px;
    max-width: 560px;
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    letter-spacing: 0.016em;
    color: var(--grey-color);
  }

  &__btns {
    display: flex;
    align-items: center;
  }

  &__image {
    position: absolute;
    top: 205px;
    right: 0;

    img {
      width: 771px;
    }
  }

  &__completed {
    margin-top: 32px;
  }
}

.completed__list {
  display: flex;
  align-items: center;
}

.completed:not(:last-child) {
  margin-right: 52px;
}

.completed__value {
  font-weight: 700;
  font-size: 40px;
  line-height: 150%;
  letter-spacing: 0.016em;
  color: var(--green-color);
}

.completed__descr {
  font-weight: 500;
  font-size: 18px;
  line-height: 180%;
  letter-spacing: 0.016em;
  color: var(--grey-color);
}

.btn-play {
  display: flex;
  align-items: center;
  padding: 7px 11px;

  svg {
    margin-right: 16px;
    fill: var(--green-color);
    width: 56px;
    height: 56px;
    object-fit: contain;
    transition: color 0.2s ease-in;

    &:hover {
      fill: #19644D;
    }
  }

  &__text {
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.016em;
    color: var(--dark-color);
    transition: color 0.2s ease-in;

    &:hover {
      color: var(--green-color);
    }
  }
}

