.recommended {
  margin: 40px 0;

  &__title {
    position: relative;
    margin: 0;
    margin-bottom: 12px;
    font-weight: 700;
    font-size: 32px;
    line-height: 120%;
    text-align: center;
    letter-spacing: 0.016em;
    color: var(--green-color);

    &:before {
      position: absolute;
      left: 8%;
      top: 50%;
      transform: translateY(-50%);
      display: block;
      content: "";
      width: 380px;
      height: 2px;
      background-color: var(--orange-color);
    }

    &:after {
      position: absolute;
      right: 8%;
      top: 50%;
      transform: translateY(-50%);
      display: block;
      content: "";
      width: 380px;
      height: 2px;
      background-color: var(--orange-color);
    }
  }

  &__logos-list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 52px 0;
  }
}

.logo__item svg {
  fill: #C7BDB3;
  transition: fill 0.2s ease-in;

  &:hover {
    fill: var(--green-color);
  }
}
