.submit {
  position: relative;
  padding: 45px 0 70px 0;

  &__heading {
    text-align: center;
    margin-bottom: 65px;
  }

  &__title {
    margin: 0;
    margin-bottom: 28px;
    color: var(--light-color);
  }

  &__descr {
    margin: 0 auto;
    max-width: 650px;
    color: #E6EFEC;
  }

  &__bg img {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 410px;
    object-fit: cover;
    top: 0;
    left: 0;
    right: 0;
  }

}

.form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 0px 24px 48px;
  margin: 0 auto;
  max-width: 656px;
  height: 69px;
  border: 1px solid #FFF8F1;
  border-radius: 5px;

  &__label {
    display: flex;
    width: 100%;
  }

  &__input {

    width: 100%;
    height: 69px;
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0.05em;
    color: var(--light-color);
    background-color: transparent;

    &::placeholder {
      color: var(--light-color);
      opacity: 0.8;
    }
  }

  &__btn {
    padding: 24px 71px;
    color: var(--light-color);
    font-weight: 800;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.05em;
    background: var(--orange-color);
    border-radius: 0px 5px 5px 0px;
    transition: background-color 0.2s ease-in;

    &:hover {
      background-color: #E14921;
    }
  }
}
