.how-it-works {
  position: relative;
  padding: 20px 0 100px 0;

  &__image {
    position: absolute;

    img {
      width: 696px;
    }
  }

  &__content {
    margin: 150px 10px auto auto;
    max-width: 590px;
  }

  &__heading {
    margin-bottom: 62px;
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
  }

  &__descr {
    margin: 0;
    max-width: 540px;
    font-size: 18px;
  }
}

.btn--choose {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 24px 70px;
  border-radius: 5px;
  font-weight: 800;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.05em;
  text-align: center;
  color: var(--orange-color);
  background-color: none;
  border: 1px solid var(--orange-color);
  cursor: pointer;
  transition: color 0.2s ease-in, background-color 0.2s ease-in;

  &:hover {
    color: var(--light-color);
    background-color: var(--orange-color);
  }
}
