.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: var(--orange-color);
  border-radius: 5px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.5;
  letter-spacing: 0.016em;
  text-align: center;
  cursor: pointer;
  color: var(--light-color);
  transition: background-color 0.2s ease-in;

  &--header {
    padding: 13px 32px;
    z-index: 1000;
  }

  &--hero {
    padding: 24px 62px;

    margin-right: 35px;
  }

  &:hover {
    background-color: #E14921;
  }
}
