.nav {
  z-index: 1000;

  &__list {
    @include flex-all-center;
  }

  &__item:not(:last-child) {
    margin-right: 56px;
  }

  &__link {
    font-weight: 500;
    font-size: 18px;
    line-height: 180%;
    color: var(--dark-color);
    transition: color 0.2s ease-in;

    &:hover {
      color: var(--orange-color);
    }
  }
}
